'use client';
import { useEffect, useRef } from 'react';

export default function Page() {
  const h1Ref = useRef<HTMLHeadingElement>(null);
  const pRef = useRef<HTMLParagraphElement>(null);

  useEffect(() => {
    // 类和对象
    class Student {
      name: string;
      age: number;
      school: string;
      grade: number;
      constructor(name: string, age: number, school: string, grade: number) {
        this.name = name;
        this.age = age;
        this.school = school;
        this.grade = grade;
      }
      learn(subject = '语文') {
        console.log(`${this.name}正在学习${subject}，真棒！`);
      }
      exam() {
        console.log(`${this.name} 正在考试`);
      }
    }
    const xiaoming = new Student('小明', 7, '实验小学', 1);
    xiaoming.learn('英语');
    xiaoming.exam();

    class Undergraduate extends Student {
      major: string;
      experience: number;
      constructor(name: string, age: number, school: string, grade: number, major: string, experience: number) {
        super(name, age, school, grade);
        this.major = major;
        this.experience = experience;
      }
      intern(corp: string) {
        console.log(`${this.name}在${corp}实习，获得经验值1000`);
        console.log(super.exam());
      }
    }
    const xiaoming2 = new Undergraduate('小明', 17, '科技大学', 1, '新闻学', 0);
    xiaoming2.intern('腾讯');

    // 回调函数，函数作为参数传递给另一个函数
    console.log('111111');
    setTimeout(function () { console.log('222222'); }, 1000);
    console.log('333333');

    // DOM 操作
    if (h1Ref.current) {
      console.log(h1Ref.current);
      h1Ref.current.innerHTML = '这是来自脚本的动态内容';
      h1Ref.current.style.color = '#888';
    }
    if (pRef.current) {
      pRef.current.onclick = function () {
        alert('你好啊！');
      };
    }
    alert('页面加载完成');
  }, []);

  return (
    <div className="practice-html">
      <h1 ref={h1Ref} style={{ cursor: 'pointer' }} onClick={() => alert('你点我干啥！')}>
        这是一个标题的原始内容
      </h1>
      <div>
        <p ref={pRef}>这是一个段落</p>
      </div>
    </div>
  );
} 